.small-grid {
  max-width: 1320px;
  margin-left: auto;
  margin-right: auto;

  &--column {
    padding: 0;
    display: flex;
    flex-direction: column;
  }
  &--play {
    border: 0;
    padding: 0;
    outline: 0;
    background-color: transparent;
    position: absolute;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
    cursor: pointer;
    transition: opacity .5s ease;
    &:hover {
      opacity: .8;
    }
    svg {
      width: 100px;
      height: 100px;
      fill: $white;
    }
  }
  &--image {
    color: $white;
    background-size: cover;
    height: 100%;
    min-height: 300px;
    display: flex;
    align-items: flex-end;
    position: relative;
    overflow: hidden;

    &-video {
      min-height: 600px;
    }

    &:before {
      z-index: 0;
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      top: 50%;
      left: 0;
      background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.8));
      position: absolute;
      transition: top .5s ease;
    }

    &--content {
      margin: 40px 40px 40px 60px;
    }

    &--title {
      margin: 0;
      @include font-size(36px);
      line-height: 1.1em;
      font-weight: 700;   
      &-large {
        @include font-size(70px);
      }   
    }

    &--text, &--pre {
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      margin: 0;
      transition: max-height .3s linear, margin-top .2s linear .1s, opacity .3s linear;
    }

    &--pre {
      display: block;
      font-size: 14px;
      line-height: 1em;
      &-visible {
        max-height: unset;
        margin-bottom: 10px;
        opacity: 1;
      }
    }

    &:hover {
      &:before {
        top: 0%;
      }

      .small-grid--image--pre,
      .small-grid--image--text {
        opacity: 1;
        max-height: 200px;
        transition: max-height .3s linear, margin-top .1s linear, opacity .3s linear;
      }

      .small-grid--image--pre {
        margin-bottom: 10px;
      }

      .small-grid--image--text {
        margin-top: 10px;
      }
    }
  }
}
