header {
  margin: -93px -15px 0;
  position: relative;
}

.header {
  &--image {
    max-width: 100%;
  }
  &--content {
    position: absolute;
    color: $white;
    margin: 90px;
    max-width: 768px;
    top: 50%;
    max-width: 870px;
    display: flex;
    align-items: center;
    transform: translateY(calc(-50% - 90px));
    &--title {
      @include font-size(100px);
      line-height: 1.1em;
      margin: 0;
      padding: 0;
    }
    &-expanded &--title {
      @include font-size(70px);
      margin-right: 20px;
      flex: 0 0 57%;
    }
    &-expanded {
      max-width: 65%;
      align-items: flex-start;
    } 
  }

  &--scroll {
    color: $white;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    &--icon {
      fill: $white;
      width: auto;
      height: 80px;
      margin-bottom: 10px;
      border: 2px solid $white;
      border-radius: 50%;
      width: 50px;
      height: 50px;
      transform: rotate(-90deg);
      padding: 7px;
    }
  }
}