.big-grid {
  color: $white;
  font-size: 16px;
  text-align: center;
  strong {
    display: block;
    @include font-size(60px);
    line-height: 1.2em;
    font-weight: 700;
    margin-bottom: 5px;
    & + strong {
      margin-top: 50px;
    }
  }

  &--column {
    background-color: $darkGreen;
  }

  &--content {
    padding: 100px 50px;
    background-size: cover;
  }

  &--main-value {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 100px;
    &:before, &:after {
      content: "";
      position: absolute;
      background-color: $white;
      height: 2px;
      width: 100%;
      right: -110%;
    }
    &:before {
      transform: rotate(-25deg);
      top: -10px;
    }
    &:after {
      transform: rotate(25deg);
      bottom: 5px;
    }
  }
  &--sub-value {
    position: absolute;
    top: 35%;
    transform: translateY(-50%);
    left: 65%;
    & ~ & {
      top: 65%;
    }
  }
}