@import "../node_modules/bootstrap/scss/bootstrap-grid.scss";
@import "../node_modules/aos/src/sass/aos.scss";
@import "../node_modules/rfs/scss";
@import "../node_modules/chart.js/dist/Chart.css";


@import "./var";
@import "components/_components__";

body {
    margin: 0;
    font-family: "Akzidenz Grotesk BQ", sans-serif;
    font-size: 16px;
    line-height: 1.1em;
    overflow-x: hidden;
}

.green-accent {
    .bar:before,
    #gri {
        background-color: $darkGreen;
    }
    .button {
        border-color: $darkGreen;
        &-solid {
            background-color: $darkGreen;
        }
    }
}

.yellow-accent {
    .bar:before,
    #gri {
        background-color: $yellow;
    }
    .button {
        border-color: $yellow;
        &-solid {
            background-color: $yellow;
        }
    }
}

.red-accent {
    .bar:before,
    #gri {
        background-color: $red;
    }
    .button {
        border-color: $red;
        &-solid {
            background-color: $red;
        }
    }
}

.blue-accent {
    .bar:before,
    #gri {
        background-color: $blue;
    }
    .button {
        border-color: $blue;
        &-solid {
            background-color: $blue;
        }
    }
}

* {
    box-sizing: border-box;
}

.wrapper {
    max-width: 1920px;
}

section {
    margin-top: 75px;
    margin-bottom: 75px;
}

.next {
    text-decoration: none;
    color: $black;
    margin: 0 auto;
    display: inline-block;
    &-icon {
        display: inline-block;
        margin: 0 5px;
        fill: $black;
        transform: rotate(-90deg);
        width: 50px;
        height: 50px;
        vertical-align: middle;
        border: 2px solid $black;
        border-radius: 50%;
        transform: rotate(-180deg);
        padding: 7px;
    }

    &-section {
        text-align: center;
    }
}

.yellow-text {
    color: $yellow;
}

.bar {
    position: relative;
    padding-left: 20px;

    &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 5px;
        height: 100%;
        border-radius: 10px;
        background-color: $darkGreen;
    }

    &-green:before {
        background-color: $darkGreen !important;
    }

    &-red:before {
        background-color: $red !important;
    }
    &-yellow:before {
        background-color: $yellow !important;
    }
    &-blue:before {
        background-color: $blue !important;
    }
}

h2.bar:before,
h1.bar:before {
    top: 15px;
    height: calc(100% - 30px);
}

.button {
    font-size: 18px;
    line-height: 1.1em;
    padding: 15px 30px;
    border-radius: 30px;
    border: 1px solid;
    color: $black;
    text-decoration: none;
    outline: 0;
    font-weight: 700;
    user-select: none;
    display: inline-block;
    background-color: transparent;
    transition: opacity .25s ease;
    position: relative;
    svg {
        vertical-align: top;
        width: 23px;
        height: 25px;
        margin: -4px 0;
    }
   
    &:hover {
        opacity: 0.7;
    }

    & + & {
        margin-left: 10px;
    }

    &-solid {
        color: $white;
    }
}
