.pagination {
  &--inner {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
  }
  .row {
    flex: 0 0 100%;
    & + .row {
      margin-left: 200px;
    }
  }
  &--first {
    transition: margin .5s ease;
    margin-left: 0;
  }

  &[data-page="0"] &--left, &--on-last-page &--right {
    opacity: .5;
    pointer-events: none;
    cursor: not-allowed;
  }

  @for $i from 1 through 10 {
    &[data-page="#{$i}"] &--first {
      margin-left: calc((#{$i} * -100%) - 200px);
    }
  }

  @for $i from 0 through 10 {
    &[data-page="#{$i}"] &--dot:nth-of-type(#{$i + 1}) {
      background-color: $yellow;
    }
  }

  &--button {
    border: 2px solid $black;
    border-radius: 50%;
    outline: none;
    cursor: pointer;
    height: 50px;
    width: 50px;
    background-color: transparent;
    margin-right: 10px;
    svg {
      width: 25px;
      height: 25px;
    }
  }
  &--dot {
    display: inline-block;
    margin: 20px 5px 0 0;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $grey;
  }
}