.text-and-image {
  &--image {
    background-size: cover;
    padding: 180px 85px;
    display: flex;
    align-items: center;
  }
  &--h2 {
    @include font-size(70px);
    line-height: 1.1em;
    color: $white;
  }
  &--texts {
    padding: 85px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &--h3 {
    @include font-size(36px);
    line-height: 1.1em;
  }
  &--lead {
    color: $grey;
    font-weight: 700;
    font-size: 20px;
  }
  p {
    font-size: 18px;
    line-height: 1.1em;
  }
}

.green-accent {
  .text-and-image--h3 {
    color: $darkGreen;
  }
}

.yellow-accent {
  .text-and-image--h3 {
    color: $yellow;
  }
}

.red-accent {
  .text-and-image--h3 {
    color: $red;
  }
}

.blue-accent {
  .text-and-image--h3 {
    color: $blue;
  }
}