#navigation {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  height: 93px;
  z-index: 1;
  margin: 0 -15px;

}

.green-accent {
  #navigation.nav-color{
    .nav-left, .nav-item--submenu {
      background-color: rgba($green, 1);
    }
  }
  .nav {
    &-left, &-item--submenu {
      background-color: rgba($green, 0);
    }
  }
}

.yellow-accent {
  #navigation.nav-color{
    .nav-left, .nav-item--submenu {
      background-color: rgba($orange, 1);
    }
  }
  .nav {
    &-left, &-item--submenu {
      background-color: rgba($orange, 0);
    }
  }
}

.red-accent {
  #navigation.nav-color{
    .nav-left, .nav-item--submenu {
      background-color: rgba($red, 1);
    }
  }
  .nav {
    &-left, &-item--submenu {
      background-color: rgba($red, 0);
    }
  }
}

.blue-accent {
  #navigation.nav-color{
    .nav-left, .nav-item--submenu {
      background-color: rgba($navBlue, 1);
    }
  }
  .nav {
    &-left, &-item--submenu {
      background-color: rgba($navBlue, 0);
    }
  }
}

.nav {
  &-left {
    transition: background-color .5s ease;
  }
  &-left, &-right {
    padding: 0 40px;
    display: flex;
  }

  &-item {
    color: $white;
    text-decoration: none;
    padding: 8px;
    position: relative;
    display: flex;
    align-items: center;
  
    &--submenu {
      position: absolute;
      top: calc(100% - 40px);
      left: 0;
      width: 400px;
      padding: 10px 20px;
      pointer-events:none;
      opacity: 0;
      visibility: hidden;
      transition: opacity .5s ease, visibility 0s ease .5s;
      &:before {
        content: "";
        position: absolute;
        top: 10px;
        left: 10px;
        width: 4px;
        height: calc(100% - 20px);
        border-radius: 10px;
        background-color: white;
      }
    }
  
    &--decorated {
      text-decoration: underline;
      margin-left: .5em
    }    
  }

  &-logo {
    background-color: $white;
    padding: 20px;
  }
}

a.nav-item {
  &:hover, &--active {
    opacity: .6;
  }
}

div.nav-item {
  &:hover, &--active {
    .nav-item--title {
      opacity: .6;
    }   
  }
  &:hover {
    .nav-item--submenu {
      transition: opacity .5s ease;
      visibility: visible;
      opacity: 1;
      pointer-events: all;
    }
  }
}
