.modal {
  &-content-template {
    display: none;
  }

  & &-content-template {
    display: block;
  }
  & &-content-template.row {
    display: flex;
  }

  position: fixed;
  width: 70%;
  max-height: 80%;
  background-color: $white;
  top: 10%;
  left: 15%;
  z-index: 11;
  padding: 50px 15px 75px;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  &-close {
    position: absolute;
    bottom: 12px;
    left: 25px;
    cursor: pointer;
  }

  &-content-template {
    height: 100%;
    overflow: hidden;
  }
  &-trigger {
    cursor: pointer;
  }
  &-video {
    display: flex;
    flex-direction: row;
    &:before {
      content: "";
      width: 0;
      padding-top: 67.25%;
      display: block;
    }
    &-frame {
      border: 0;
      flex: 0 0 100%;
    }
  }
}

body:after {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0);
  z-index: -1;
  transition: background-color .5s ease, z-index 0s ease .5s;
  pointer-events: none;
}

.modal-open:after {
  background-color: rgba(0,0,0,0.75);
  z-index: 10;
  transition: background-color .5s ease, z-index 0s ease;
  pointer-events: all;
}

.modal-text-and-image {
  &--text {
    padding: 0 70px;
    max-height: 500px;
    overflow: auto;
  }

  &--image {
    align-items: center;
    display: flex;
    padding-right: 190px;
  }
}