#gri {
  color: $white;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 40px;
  width: calc(100% - 80px);
  max-width: 1840px;
  padding: 10px 20px;
  justify-content: space-between;
  @media (min-width: 1940px) {
    left: calc(50vw - 930px);
  }
}

.gri {
  &-left {
    font-weight: 700;
  }

  &-figures {
    position: relative;
    flex-grow: 1;
  }

  &-figure {
    position: absolute;
    opacity: 0;
    left: 50%;
    transform: translateX(-50%);
    transition: opacity .5s ease;
    &--active {
      transition: opacity .5s ease .5s;
      opacity: 1;
    }
  }
  &-right {
    font-weight: 700;
    cursor: pointer;
    svg {
      fill: $white;
      width: 25px;
      height: auto;
      margin: -9px 0;
    }
  }
}